
@charset "UTF-8";
/**引入全局样式 */
/** 通用颜色定义 */
/**
 * 通用颜色样式
 */
/** 引入bootstrap样式 */
@import url("//cdn.bootcss.com/bootstrap/3.3.4/css/bootstrap.min.css");
/** 引入图标库 */
@import url("//at.alicdn.com/t/font_9i59uu0o58w7b9.css");
/** 全局通用样式定义 */
/** 文字样式 **/
body {
  font-family: Microsoft Yahei;
}
.text-white {
  color: #FFFFFF;
}
.text-red {
  color: #ff4949;
}
.text-gray {
  color: #D3DCE6;
}
.text-green {
  color: #23BC61;
}
.text-blue {
  color: #20A0FF;
}

/** 文字大小 **/
/** 动画特效 **/
.fade-transition-enter-active, .fade-transition-leave-active {
  transition: opacity .3s ease;
}
.fade-transition-enter, .fade-transition-leave-active {
  opacity: 0;
}
@keyframes dash {
from {
    stroke-dashoffset: 250;
    /* 这里是svg图形中素描线长度，可以使用js获取 */
}
to {
    stroke-dashoffset: 0;
}
}

/* 支持chrome浏览器 */
#content {
  background: #148bcd;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
#footer {
  width: 100%;
  background: #fff;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  bottom: 0;
}
#footer p {
    margin: 0;
    line-height: 40px;
}
@media (max-width: 720px) {
#content > .container {
    width: 100%;
    padding: 0;
}
}



