
@charset "UTF-8";
#header {
  background: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
#header .logo {
    width: 70px;
    height: 70px;
    float: left;
}
#header .logo img {
      height: 70px;
}
#header .menu {
    float: left;
    line-height: 70px;
}
#header .menu .active {
    color: #1197e2;
}
#header .menu li {
    float: left;
    width: 60px;
    line-height: 70px;
    list-style: none;
    text-align: center;
}
#header .menu li a {
      display: block;
      color: #000;
      text-decoration: none;
}
#header .menu li a:hover {
      text-decoration: underline;
}
#header .menu li a:focus {
      color: #0089ff;
}
#header .nav-toggle {
    display: none;
    float: right;
    line-height: 70px;
}
#header .nav-toggle i {
    font-size: 2em;
}
#header .text-logo {
    float: right;
    width: 200px;
    height: 70px;
}
#header #path {
    color: #fff;
    stroke-dasharray: 265.07;
    stroke-dashoffset: 250;
    animation: dash 5s linear infinite;
    -webkit-animation: dash 5s linear infinite;
}

/**小屏幕适配 */
@media (max-width: 720px) {
#header .nav-toggle {
    display: block;
}
#header .menu, #header .text-logo {
    display: none;
}
}



