
.sidebar {
  position: fixed;
  top: 0;
  right: -80%;
  transition: all ease .3s;
  background: #164062;
  z-index: 10001;
  width: 80%;
  height: 100%;
}
.sidebar .sidebar-inner {
    padding: 20px 0;
}
.sidebar .site-author {
    text-align: center;
}
.sidebar .site-author .site-author-image {
      width: 100px;
      height: 100px;
}
.sidebar .site-author .site-author-name {
      font-family: Brush Script MT;
      font-size: 1.5em;
      color: #d5d602;
      line-height: 30px;
      text-align: center;
}
.sidebar .liner-split {
    height: 1px;
    background: linear-gradient(to left, #164062, #fff, #164062);
}
.sidebar .menu ul {
    margin: 0;
    padding: 0;
}
.sidebar .menu li {
    list-style: none;
    text-align: center;
    line-height: 60px;
}
.sidebar .menu li a {
      color: #fff;
}
.sidebar .sidebar-close {
    position: absolute;
    width: 100%;
    bottom: 30px;
}
.sidebar .sidebar-close i {
    font-size: 36px;
    color: #fff;
}



