
.music-box {
	height:500px;
  border-top:1px solid #aaa;
	padding:0;
	max-width:420px;
	margin:auto;
}
#music .top {
	height:70px;
	border-radius:4px 4px 0 0;
	background:#fff;
}
#music .center {
	min-height:385px;
	background:#fff;
	border-bottom:1px solid #aaa;
	border-top:1px solid #aaa;
}
#music .bottom {
	height:35px;
	background:#fff;
	padding:0 30px;
	border-radius:0 0 4px 4px;
  border-top: 1px solid #aaa;
}
#music .right {
	//width:220px;
	overflow:hidden;
}
#music .fleft {
	float:left;
}
#music .fright {
	float:right;
}
#music .whalf {
	width:48%;
}
#music .song-name {
	width:65%;
  float: left;
	white-space: nowrap;
	overflow: hidden;
}
#music .song-time {
	width:28%;
}
#music .song-control li {
	list-style:none;
	font-size:30px;
	margin:0 10px;
	color:#888;
	padding-top:5%;
	float:left;
}
#music .progress {
  width: 100%;
	height:3px;
}
#music .song-center {
	width:100%;
	overflow-y:auto;
	overflow-x:hidden;
	//padding:10px;
	background:#fff;
	border-top:1px solid #aaa;
	height:380px;
}
#music .song-wrap {
	width:200%;
	    transition: all .3s ease-in-out;
}
#music ul {
	padding:0;
	transition: all .3s ease-in-out;
}
#music .song-list {
	transition: all .3s ease-in-out;
}
#music .song-list li {
	list-style:none;
	padding-left:5px;
	line-height:35px;
	border-bottom:1px solid #ccc;
}
#music .song-list li:hover {
	background:#eee;
	cursor:pointer;
}
#music .song-list li.active {
	color:#fff;
	background:rgb(20,80,132);
}
#music .song-list,.song-lrc {
	float:left;
	height:375px;
	overflow:auto;
	width:50%;
}
#music .song-lrc {
	background:rgba(0,0,0,0.8);
	color:#ccc;
	font-size:12px;
	padding-top:150px;
	height:375px;
	overflow:hidden;
}
#music .song-lrc .active {
	color: #0f0;
}
#music .share-qzone ,.pp,.forward,.lrc-list,.play-type {
	cursor:pointer;
}
#music .share-qzone:hover,.pp:hover,.forward:hover,.lrc-list:hover,.play-type:hover {
	box-shadow:0 0 4px #000;
}
#music .lrc-content {
	transition: all .3s ease;
}
#music .lrc-content p {
	white-space:nowrap;
	overflow:hidden;
}
#music .transform-lrc {
  transition: all .ease .3s;
  transform: translateX(-50%);
}



