
#more {
  width: 100%;
  height: 100%;
  color: #fff;
}
#more a {
    color: #000;
}
#more .section {
    margin: 40px 0;
}
#more .section .section-header {
      padding: 10px 0;
}
#more .section .section-content {
      border-top: solid 1px #aac6d5;
      padding-top: 10px;
}
#more .links {
    display: inline-block;
}
#more .links .url-pic, #more .links .url-info {
      width: 100px;
      height: 102px;
      position: absolute;
      line-height: 100px;
      text-align: center;
}
#more .links .url-info:hover {
      background: rgba(0, 0, 0, 0.5);
      border: solid 2px #aa0;
      color: #fff;
}
#more .links li {
      list-style: none;
      float: left;
      width: 100px;
      height: 100px;
      margin: 0 20px 20px 0;
}
#more .qr {
    margin: 20px 0;
}
#more .qr img {
      max-width: 150px;
      max-height: 150px;
      box-shadow: 1px 0px 20px 6px #fff;
}
#more .qr p {
      margin-top: 10px;
      text-align: center;
      text-shadow: 1px -1px 20px #000;
}



